.blog {
  margin: 70px auto;
  padding: 20px;
}

.header {
  padding-bottom: 50px;
}

.header__title {
  font-size: 35px;
  font-weight: bolder;
  padding-bottom: 20px;
}

.header__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__newpost {
  margin-right: 50px;
}

.blog__post {
  display: flex;
  justify-content: space-between;
  white-space: nowrap;
  transition: 0.2s;
}

.blog__post:hover {
  transform: scale(1.025);
  z-index: 1;
  box-shadow: 0 0 40px 0 rgba(0, 0, 0, 0.075);
}

.blog__title {
  overflow: hidden;
  text-overflow: ellipsis;
  min-width: 20%;
}

.blog__title:hover {
  color: #6c757d;
  cursor: pointer;
  text-decoration: underline;
}

.btn ~ .btn {
  margin-left: 10px;
}
